.container {
  width: 100%;
  height: 89vh;
  border: 1px solid black;
  background-image: url('https://s3-alpha-sig.figma.com/img/8b93/6aad/1a8a9bb19893d9b0ef5acb10326ff9b1?Expires=1721606400&Key-Pair-Id=APKAQ4GOSFWCVNEHN3O4&Signature=GvPqxQ~mhBNnABSlSKvJNXsewnSoKc03DG1iHVeBXShdbXskORjNfw5mODuxUvDEe5Jj~ThyKKzkkAa~B90P9bJYOqAB4qqcWrfIdryV~X5IsFY87jrkzFuWgnR2mzzYbWXd8~gL1IC8IsTiShDsHAwdR6unIl8Uc4ImPHffGspgXueP4n27Ty0xP5VksJ9x33F1Hs8qNP~RdEn8rUYlmxgZY119y7fCbX3tLSpLMAtDfugKip5vxvq9zAVBJJBa10O0YcX~nf7T~PGhBSGh8z2GcwrbKjYS7FWzbGhSS-css~9utAQnBF76eyYwjYqMiJiSEXsvWCPiWixZXY7adg__');
  display: flex;
  flex-direction: column;
  align-items: center;
}

.form {
  margin-top: 80px;
  display: flex;
  flex-direction: row;
  width: 710px;
  height: 48px;
  gap: 14px;
}

.input {
  width: 550px;
  padding: 12px 12px 12px 20px;
  gap: 10px;
  border-radius: 40px;
  background-color: #FFFFFF1A;
  border: 1px dashed white;
  font-size: 20px;
  color: white;
}

.input:hover,
.input:focus,
.input:active {
  background-color: #FFFFFF1A !important;
  border: 1px dashed rgb(14, 1, 1);
}

.btnSearch {
  width: 146px;
  height: 48px;
  padding: 12px 40px;
  gap: 10px;
  border-radius: 50px;
  background: rgba(54, 120, 180, 1);
  font-size: 20px;
  color: #fdfcfc;
  border: none;
  cursor: pointer;
}

.outputInfo {
  position: relative;
  top: 150px;
  padding: 10px;
  width: 709px;
  height: 220px;
  background-color: rgba(29, 53, 89, 0.441);
  border-radius: 20px;
  display: flex;
}

.outputInfo p {
  margin: 10px;
  position: relative;
  top: 0px; 
  left: 0px;
  color: white;
  font-size: 57px;
}

.outputInfo h3 {
  margin: 2px;
  position: relative;
  top: 90px; 
  left: -120px;
  color: white;
  font-size: large;

}

.outputInfo img {
  position: relative;
  top: 30px; 
  left: 200px;
}

.containerSaveDelete {
  position: relative;
  top: 150px; 
  left: -100px;
  display: flex;
  gap: 20px;
}

.btnSaveDelete {
  width: 155px;
  height: 48px;
  padding: 12px 40px;
  gap: 10px;
  border-radius: 50px;
  border: 1px solid transparent;
  background-color: #3460db89;
  color: #fff;
  font-size: 17px;
  cursor: pointer;
}

.loader {
  width: 48px;
  height: 48px;
  border: 15px solid #fff;
  border-bottom-color: #ff3d00;
  border-top-color: #35089d;
  border-radius: 50%;
  display: inline-block;
  box-sizing: border-box;
  animation: rotation 0.7s linear infinite;
  position: absolute;
  top: 50%;
  left: 50%;
}

@keyframes rotation {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}
